<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList" @input="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialog = true"></el-button>
        </el-col>
      </el-row>

      <!--      -->
      <el-table :data="userlist" border stripe>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="" prop="mg_state">
          <template v-slot="scope">
            <el-switch v-model="scope.row.mg_state" @change="userState(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <!--            -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" circle @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
            <!--            -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" circle @click="removeUserByid(scope.row.id)"></el-button>
            </el-tooltip>
            <!--            -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="warning" icon="el-icon-star-off" circle></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 7, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!--    -->
    <el-dialog title="" :visible.sync="addDialog" width="50%" @close="AddLogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px">
        <!--        -->
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
    <el-button @click="addDialog = false"> </el-button>
    <el-button type="primary" @click="AddUser"> </el-button>
  </span>
    </el-dialog>
    <!--    -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUserInfo"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
    export default {
        name: "Uesrs",
        data() {
            //

            // 
            var checkMobile = (rule, value, cb) => {
                const regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
                if (regMobile.test(value)) {
                    return cb()
                }
                cb(new Error(''))
            };
            return {
                //
                queryInfo: {
                    query: '',
                    //
                    pagenum: 1,
                    //
                    pagesize: 2
                },
                userlist: [],
                total: 0,
                //
                addDialog: false,
                //
                editDialogVisible: false,
                //
                addForm: {
                    username: '',
                    password: '',
                    email: '',
                    mobile: '',
                },
                //
                editForm: {
                    email: '',
                    mobile: '',
                },
                //
                addFormRules: {
                    username: [
                        {required: true, message: '', trigger: 'blur'},
                        // {min: 4, max: 12, message: ' 4  12 ', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 4, max: 12, message: ' 4  12 ', trigger: 'blur'}
                    ],
                    email: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 4, max: 12, message: ' 4  12 ', trigger: 'blur'},
                    ],
                    mobile: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 11, max: 11, message: '11', trigger: 'blur'},
                        {validator: checkMobile, trigger: 'change'}
                    ],
                }
            }
        },
        created() {
            this.getUserList()
        },
        methods: {
            async getUserList() {
                const {data: res} = await this.$http.get('users', {params: this.queryInfo});
                if (res.meta.status !== 200) {
                    return this.$message.error("")
                }
                this.userlist = res.data.users
                this.total = res.data.total
                console.log(res)
            },
            // page
            handleSizeChange(newSize) {
                this.queryInfo.pagesize = newSize
                this.getUserList()
            },
            //
            handleCurrentChange(newPage) {
                this.queryInfo.pagenum = newPage;
                this.getUserList()
            },
            async userState(userInfo) {
                console.log(userInfo);
                const {data: res} = await this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`)
                console.log(res.meta.status)
                if (res.meta.status !== 200) {
                    userInfo.mg_state = !userInfo.mg_state
                    return this.$message.error("")
                }
                this.$message.success("")
            },
            //
            AddLogClosed() {
                this.$refs.addFormRef.resetFields()
            },
            //
            AddUser() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return;
                    const {data: res} = await this.$http.post('users', this.addForm)

                    if (res.meta.status !== 201) {
                        this.$message.error('')
                    }
                    this.$message.success("")
                    //
                    this.addDialog = false
                    //
                    this.getUserList()

                })
            },
            //
            async showEditDialog(id) {
                console.log(id)
                const {data: res} = await this.$http.get('users/' + id)
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.editForm = res.data;
                this.editDialogVisible = true;
            },
            //
            editUserInfo() {
                this.$refs.editFormRef.validate(async validate => {
                    if (!validate) return;
                    console.log(validate)
                    const {data: res} = await this.$http.put('users/' + this.editForm.id,
                        {
                            email: this.editForm.email,
                            mobile: this.editForm.mobile
                        });
                    console.log(res.meta.status)
                    if (res.meta.status !== 200) {
                        return this.$message.error("")
                    }
                    this.editDialogVisible = false;
                    this.$message.success("")
                    this.getUserList()

                })
            },
            //
            async removeUserByid(id) {
                const ctr = await this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)

                if (ctr !== 'confirm') {
                    return this.$message.info('')
                }
                const {data: res} = await this.$http.delete('users/' + id)

                if (res.meta.status !== 200) {
                    return this.$message.error("")
                }

                this.$message.success("")
                this.getUserList()
            }
        }
    }
</script>

<style lang="less" scoped>
  .el-breadcrumb {
    margin-bottom: 20px;
  }



  .el-pagination {
    margin-top: 15px;
    text-align: center;
  }
</style>
