<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--    -->
    <el-card>
      <!--      -->
      <el-alert title="" type="warning" show-icon :closable="false">
      </el-alert>
      <!--      -->
      <el-row class="shop_row">
        <el-col>
          <span></span>
          <el-cascader
            v-model="selectedCatKeys"
            :options="cateList"
            :props="cateProps"
            @change="handleChange"></el-cascader>
        </el-col>
      </el-row>
      <!--      tabs -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!--        -->
        <el-tab-pane label="" name="many">
          <el-button type="primary" :disabled="isBtnDisabled" @click="primaryVisible = true"></el-button>
          <!--          -->
          <el-table :data="manyTableData" border stripe>
            <!--            -->
            <el-table-column type="expand" align="center">
              <template slot-scope="scope">
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag
                </el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" align="center"></el-table-column>
            <el-table-column label="" prop="attr_name" align="center"></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" plain @click="showEditlog(scope.row.attr_id)">
                  
                </el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" plain
                           @click="removeParams(scope.row.attr_id)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--        -->
        <el-tab-pane label="" name="only">
          <el-button type="primary" :disabled="isBtnDisabled" @click="primaryVisible = true"></el-button>
          <!--          -->
          <el-table :data="onlyTableData" border stripe>
            <el-table-column type="expand" align="center">
              <template slot-scope="scope">
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag
                </el-button>
              </template>
            </el-table-column>
            <el-table-column type="index" align="center"></el-table-column>
            <el-table-column label="" prop="attr_name" align="center"></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" plain @click="showEditlog(scope.row.attr_id)">
                  
                </el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" plain
                           @click="removeParams(scope.row.attr_id)">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--    /-->
    <el-dialog :title="''+titleText" :visible.sync="primaryVisible" width="50%" @close="addDialogClose">
      <!--      -->
      <el-form :model="addFrom" :rules="addFromRules" ref="addFromRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addFrom.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="primaryVisible = false"> </el-button>
    <el-button type="primary" @click="addParams"> </el-button>
  </span>
    </el-dialog>
    <!--    /-->
    <el-dialog :title="''+titleText" :visible.sync="editprimaryVisible" width="50%" @close="editDialogClose">
      <!--      -->
      <el-form :model="editFrom" :rules="editFromRules" ref="editFromRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editFrom.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editprimaryVisible = false"> </el-button>
    <el-button type="primary" @click="editParams"> </el-button>
  </span>
    </el-dialog>


  </div>
</template>

<script>
    export default {
        name: "Params",
        data() {
            return {
                //
                cateList: [],
                //
                cateProps: {
                    value: 'cat_id',
                    label: 'cat_name',
                    children: 'children',
                    expandTrigger: 'hover'
                },
                //
                selectedCatKeys: [],
                //
                activeName: 'many',
                //
                manyTableData: [],
                //
                onlyTableData: [],
                //
                primaryVisible: false,
                //
                editprimaryVisible: false,

                addFrom: {
                    attr_name: ''
                },
                //
                addFromRules: {
                    attr_name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                },
                editFrom: {},
                editFromRules: {
                    attr_name: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                }
            }
        },
        created() {
            this.getCateList()
        },
        methods: {
            // 
            async getCateList() {
                const {data: res} = await this.$http.get('categories')
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.cateList = res.data
                console.log(this.cateList)


            },
            //
            handleChange() {
                this.getParamsData()
            },
            //
            handleTabClick() {
                this.getParamsData()
            },
            //
            async getParamsData() {
                if (this.selectedCatKeys.length !== 3) {
                    this.selectedCatKeys = []
                    return
                }

                const {data: res} = await this.$http.get(`categories/${this.cateId}/attributes`,
                    {params: {sel: this.activeName}})
                if (res.meta.status !== 200) {
                    return this.$message.error("")
                }


                res.data.forEach(item => {
                    item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : []
                    // 
                    item.inputVisible = false
                    // 
                    item.inputValue = ''

                })

                console.log(res.data)

                if (this.activeName === 'many') {
                    this.manyTableData = res.data
                } else {
                    this.onlyTableData = res.data
                }
            },
            //
            addDialogClose() {
                this.$refs.addFromRef.resetFields()
            },
            editDialogClose() {
                this.$refs.editFromRef.resetFields()
            },
            //
            addParams() {
                this.$refs.addFromRef.validate(async valid => {
                    if (!valid) return
                    const {data: res} = await this.$http.post(`categories/${this.cateId}/attributes`, {
                        attr_name: this.addFrom.attr_name,
                        attr_sel: this.activeName
                    })

                    if (res.meta.status !== 201) {
                        return this.$message.error('')
                    }
                    this.$message.success('')
                    this.primaryVisible = false
                    this.getParamsData()
                })
            },
            //
            async showEditlog(attr_id) {
                const {data: res} = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`,
                    {params: {attr_sel: this.activeName}})

                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.editFrom = res.data
                this.editprimaryVisible = true

            },
            //
            editParams() {
                this.$refs.editFromRef.validate(async valid => {
                    if (!valid) return
                    const {data: res} = await this.$http.put(`categories/${this.cateId}/attributes/${this.editFrom.attr_id}`,
                        {attr_name: this.editFrom.attr_name, attr_sel: this.activeName})

                    if (res.meta.status !== 200) {
                        return this.$message.error('')
                    }
                    this.$message.success('')
                    this.getParamsData()
                    this.editprimaryVisible = false
                })

            },
            //
            async removeParams(attr_id) {
                const confResult = await this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)

                if (confResult !== 'confirm') {
                    return this.$message.info('')
                }
                const {data: res} = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
                if (res.meta.status !== 200) {
                    this.$message.error('')
                }
                this.$message.success('');
                this.getParamsData()
            },
            //
            async handleInputConfirm(row) {
                if (row.inputValue.trim().length === 0) {
                    row.inputValue = '';
                    row.inputVisible = false;
                    return
                }
                row.attr_vals.push(row.inputValue.trim());
                row.inputValue = '';
                row.inputVisible = false;
                this.saveAttrVals(row)

                console.log('ok')
            },
            // attr_vals 
            async saveAttrVals(row){
                const {data: res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,
                    {
                        attr_name: row.attr_name,
                        attr_sel: row.attr_sel,
                        attr_vals: row.attr_vals.join(' ')
                    })
                if(res.meta.status !== 200){
                    return this.$message.error("")
                }

                this.$message.success("")
            },
            //
            showInput(row) {
                row.inputVisible = true;

                //
                //$nextTick 
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },
            //
            handleClose(i,row){
             row.attr_vals.splice(i,1)
                this.saveAttrVals(row)
            }
        },
        computed: {
            //truefalse
            isBtnDisabled() {
                if (this.selectedCatKeys.length !== 3) {
                    return true
                }
                return false
            },
            // Id
            cateId() {
                if (this.selectedCatKeys.length === 3) {
                    return this.selectedCatKeys[2]
                }
                return null
            },
            //
            titleText() {
                if (this.activeName === 'many') {
                    return ''
                } else {
                    return ''
                }
            },

        }
    }
</script>

<style scoped>
  .shop_row {
    margin: 15px 0;
    font-size: 14px;
  }

  .el-tag {
    margin-right: 10px;
    margin-top: 10px;
  }

  .input-new-tag {
    width: 130px;
    display: inline-block;
  }
</style>
