<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--      -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>
      <!--      -->
      <el-table :data="goodsList" border stripe>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="" prop="goods_price" width="85px" align="center"></el-table-column>
        <el-table-column label="" prop="goods_weight" width="80px" align="center"></el-table-column>
        <el-table-column label="" prop="add_time" width="170px" align="center">
          <template slot-scope="scope">
            {{scope.row.add_time | dataFormat}}
          </template>
        </el-table-column>
        <el-table-column label="" width="120px" align="center">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeByid(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total" background>
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
    export default {
        name: "Goods",
        data() {
            return {
                queryInfo: {
                    query: '',
                    pagenum: 1,
                    pagesize: 10
                },
                //
                goodsList: [],
                //
                total: 0,

            }
        },
        created() {
            this.getGoodsList()
        },
        methods: {
            //
            async getGoodsList() {
                const {data: res} = await this.$http.get('goods', {params: this.queryInfo})
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.$message.success("")
                console.log(res.data)
                this.goodsList = res.data.goods
                this.total = res.data.total
            },
            handleSizeChange(newSize) {
                this.queryInfo.pagesize = newSize
                this.getGoodsList()
            },
            handleCurrentChange(newPage) {
                this.queryInfo.pagenum = newPage
                this.getGoodsList()
            },
            async removeByid(id){
               const conf =  await this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch( err => err)

                if(conf !== 'confirm'){
                   return  this.$message.info('')
                }

               const {data: res} = await this.$http.delete(`goods/${id}`)

                if(res.meta.status !== 200){
                   return  this.$message.error('')
                }
                this.getGoodsList()
                this.$message.success("")
            },
            goAddpage(){
                this.$router.push('/goods/add')
            }
        }
    }
</script>

<style scoped>

</style>
