<template>
  <div class="login_cont">
    <div class="login_box">
      <!---->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="">
      </div>
      <!---->
      <el-form ref="loginFromRef" :model="loginForm" :rules="loginForm_rules"
               label-width="0px" class="login_from">
        <!---->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prop="name" prefix-icon="el-icon-s-custom"></el-input>
        </el-form-item>
        <!---->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-unlock" type="password"></el-input>
        </el-form-item>
        <!-- -->
        <el-form-item class="btnS">
          <el-button type="primary" @click="login('loginFromRef')"></el-button>
          <el-button type="info" @click="resetLoginForm('loginFromRef')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
    export default {
        name: 'login',
        data() {
            return {
                //
                loginForm: {
                    username: 'admin',
                    password: '123456',
                },
                //
                loginForm_rules: {
                    // 
                    username: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 5, max: 12, message: ' 5  12 ', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 6, max: 12, message: ' 5  12 ', trigger: 'blur'}
                    ],
                }
            }
        },
        methods: {
            //
            resetLoginForm() {
                console.log(this);
                this.$refs.loginFromRef.resetFields();
                // this.$refs["loginFromRef"].resetFields()  
            },
            login() {
                //
                this.$refs.loginFromRef.validate(async valid => {
                    //
                    if (!valid) return;
                    const {data: res} = await this.$http.post("login", this.loginForm);
                    // 200
                    if (res.meta.status !== 200) {
                        return this.$message.error("")
                    } else {
                        //1. token sessionStorage 
                        //2.api
                        //3.token token sessionStorage
                        //4./home
                        this.$message.success("");
                        window.sessionStorage.setItem('token', res.data.token);
                        this.$router.push('/Home')
                    }

                })

            }
        }
    }
</script>

<style lang="less" scoped>
  .login_cont {
    background-color: #2b4b6b;
    height: 100%;
  }

  .login_box {
    width: 450px;
    height: 350px;
    background-color: #fff;
    border-radius: 5px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }

  .btnS {
    display: flex;
    justify-content: center;
  }

  .login_from {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 30px;
    box-sizing: border-box;
  }
</style>
