<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!--        -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="roleLogVisible = true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="rolelist" border stripe>
        <el-table-column type="expand" align="center"></el-table-column>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="roleName" align="center"></el-table-column>
        <el-table-column label="" prop="roleDesc" align="center"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRole(scope.row.id)"></el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--    -->
    <el-dialog title="" :visible.sync="roleLogVisible" width="40%" @close="AddRoleClosed">
      <!--      -->
      <el-form :model="roleForm" ref="addroleFormRef" :rules="addRoleFormRules" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="roleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="roleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="roleLogVisible = false"> </el-button>
    <el-button type="primary" @click="AddRole"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
    export default {
        name: "roles",
        data() {
            return {
                //
                rolelist: [],
                //
                roleLogVisible: false,
                //
                roleForm: {
                    roleName: '',
                    roleDesc: ''
                },
                //
                addRoleFormRules: {
                    roleName: {required: true, message: '', trigger: 'blur'},

                    roleDesc: {required: true, message: '', trigger: 'blur'},
                }
            }
        },
        created() {
            this.getRolelist()
        },
        methods: {
            async getRolelist() {
                const {data: res} = await this.$http.get('roles')
                if (res.meta.status !== 200) {
                    return this.$message.error('!')
                }
                this.rolelist = res.data

            },
            //
            AddRoleClosed() {
                this.$refs.addroleFormRef.resetFields()
            },
            //
            AddRole() {
                this.$refs.addroleFormRef.validate(async valid => {
                    if (!valid) return;
                    const {data: res} = await this.$http.post('roles', this.roleForm)
                    if (res.meta.status !== 201) {
                        this.$message.error('')
                    }
                    this.$message.success("!")
                    this.roleLogVisible = false
                    this.getRolelist()
                })
            },
            //
            async removeRole(id) {
                const removeRoleV = await this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).catch(err => err)
                console.log(removeRoleV)
                if (removeRoleV !== 'confirm') {
                    return this.$message.info('')
                }

                const {data: res} = await this.$http.delete('roles/' + id)
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.$message.success("")
                this.getRolelist()
            }
        }
    }
</script>

<style scoped>

</style>
