<template>
  <div>
  <!--  -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <!---->
  <el-card>
    <el-table :data="rigthsList" border stripe>
    <el-table-column type="index" align="center"></el-table-column>
      <el-table-column label="" prop="authName" align="center"></el-table-column>
      <el-table-column label="" prop="path" align="center"></el-table-column>
      <el-table-column label="" prop="level" align="center">
        <template slot-scope="scope">
        <el-tag  effect="dark" v-if="scope.row.level === '0'"></el-tag>
        <el-tag type="success" effect="dark" v-if="scope.row.level === '1'"></el-tag>
        <el-tag type="danger" effect="dark" v-if="scope.row.level === '2'"></el-tag>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
  </div>
</template>

<script>
    export default {
        name: "Rigths",
        data() {
            return {
                //
                rigthsList: []
            }
        },
        created() {
            //
            this.getRightsList()
        },
        methods: {
            async getRightsList() {
                const {data: res} = await this.$http.get('rights/list')
                if (res.meta.status !== 200) {
                    return this.$message.error("!")
                }
                this.rigthsList = res.data
                console.log(this.rigthsList)
            }
        },
    }
</script>

