<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="4">
          <el-button type="primary" @click="showAddCatelog"></el-button>
        </el-col>
      </el-row>
      <!--        -->
      <tree-table :data="cateList" :columns="columns" :show-index="true"
                  :selection-type="false" :expand-type="false" index-text="#" class="tree-table" border>
        <!--        -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" style="color: lightgreen " v-if="scope.row.cat_deleted === false"></i>
          <i class="el-icon-error" style="color: red" v-else></i>
        </template>
        <!--        -->
        <template slot="sequence" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag v-else-if="scope.row.cat_level === 1" type="success"></el-tag>
          <el-tag v-else type="warning"></el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
        </template>
      </tree-table>
      <!--      -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total" type="center">
      </el-pagination>
      <!--      -->
      <el-dialog title="" :visible.sync="addCateLogVisible" width="50%" @close="addCateDiaClose">
        <!--        -->
        <el-form :model="addCateForm" :rules="CateFormRules" ref="CateFormRef" label-width="100px">
          <el-form-item label=" :" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label=" :">
            <!--options -->
            <el-cascader
              v-model="selectedKeys"
              :options="parentCateList"
              :props="CasCaDerProps"
              @change="parentCateChange"></el-cascader>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
    <el-button @click="addCateLogVisible = false"> </el-button>
    <el-button type="primary" @click="addParentCate"> </el-button>
  </span>
      </el-dialog>
    </el-card>
  </div>

</template>

<script>
    export default {
        name: "Cate",
        data() {
            return {
                querInfo: {
                    type: 3,
                    pagenum: 1,
                    pagesize: 5
                },
                //
                cateList: [],
                total: 0,
                addCateLogVisible: false,
                //table
                columns: [{
                    label: '',
                    prop: 'cat_name',
                },
                    {
                        label: '',
                        type: 'template',
                        template: 'isok'
                    },
                    {
                        label: '',
                        type: 'template',
                        template: 'sequence'
                    },
                    {
                        label: '',
                        type: 'template',
                        template: 'opt'
                    }
                ],
                CateFormRules: {
                    cat_name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                },
                addCateForm: {
                    cat_name: '',
                    cat_id: '',
                    cat_leven: ''
                },
                //
                parentCateList: [],

                CasCaDerProps: {
                    expandTrigger: 'hover',
                    value: 'cat_id',
                    label: 'cat_name',
                    children: 'children'
                },
                selectedKeys: [],

            }
        },
        created() {
            this.getCateList()
        },
        methods: {
            //
            async getCateList() {
                const {data: res} = await this.$http.get('categories', {params: this.querInfo})
                if (res.meta.status !== 200) {
                    return this.$message.error('')

                }
                console.log(res.data)
                //cateList
                this.cateList = res.data.result
                this.total = res.data.total
            },
            // pagesize 
            handleSizeChange(newSize) {
                this.querInfo.pagesize = newSize
                this.getCateList()
            },
            //  pagenum
            handleCurrentChange(newnum) {
                this.querInfo.pagenum = newnum
                this.getCateList()
            },

            // 
            async getParentCateList() {
                const {data: res} = await this.$http.get('categories', {params: {type: 2}})

                if (res.meta.status !== 200) {
                    return this.$message.error("")
                }
                this.parentCateList = res.data
            },
            showAddCatelog() {
                this.getParentCateList()
                this.addCateLogVisible = true

            },
            //
            parentCateChange() {
                // selectedKeys  length 0
                //
                console.log(this.selectedKeys)
                if (this.selectedKeys.length > 0) {
                    //id
                    this.addCateForm.cat_id =
                        this.selectedKeys[this.selectedKeys.length - 1]
                    //
                    this.addCateForm.cat_leven = this.selectedKeys.length;
                } else {
                    //id
                    this.addCateForm.cat_id = 0
                    //
                    this.addCateForm.cat_leven = 0
                }
            },
            //
            addParentCate() {
                this.$refs.CateFormRef.validate(async valid => {
                    if (!valid) return
                 const {data: res} = await this.$http.post('categories',this.addCateForm)
                    console.log(res)
                    if (res.meta.status !== 201){
                        return this.$message.error("")
                    }
                    this.$message.success("")
                    this.getCateList()
                    this.addCateLogVisible = false
                })

            },
            //
            addCateDiaClose() {
                this.$refs.CateFormRef.resetFields()
                this.selectedKeys = []
                this.addCateForm.cat_id = this.addCateForm.cat_leven = 0
            }
        }
    }
</script>

<style scoped>
  .tree-table {
    margin-top: 15px;
  }

  .el-pagination {
    margin-top: 15px;
    text-align: center;
  }
</style>
