<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center
                :closable="false" show-icon effect="dark">
      </el-alert>
      <!--    -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--    tab-->
      <el-form :model="addFrom" :rules="addFromRules" ref="addFormRef" label-width="100px" label-position="top">
        <el-tabs :tab-position="'left'" v-model="activeIndex" :before-leave="beforeTabLeave" @tab-click="tabClick">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addFrom.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addFrom.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addFrom.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addFrom.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addFrom.goods_cat"
                :options="cateList"
                :props="catProps"
                @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!--            item-->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!--              -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb, i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!--            -->
            <!--            actionAPI-->
            <el-upload
              action="https://www.liulongbin.top:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj" :on-success="handSuccess">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--            -->
            <quill-editor v-model="addFrom.goods_introduce"></quill-editor>
<!--            -->
            <el-button type="primary" @click="Add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--    -->
    <el-dialog
      title=""
      :visible.sync="previewPathVisible"
      width="50%">
      <img :src="previewPath" alt="" class="PImg">
    </el-dialog>
  </div>
</template>

<script>
  import _ from 'lodash'

    export default {
        name: "Add",
        data() {
            return {
                activeIndex: '0',
                //
                addFrom: {
                    goods_name: '',
                    goods_price: 1,
                    goods_weight: 1,
                    goods_number: 1,
                    goods_cat: [],
                    //
                    pics: [],
                    //
                    goods_introduce:'',
                    attrs:[]
                },
                addFromRules: {
                    goods_name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    goods_price: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    goods_weight: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    goods_number: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    goods_cat: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                //
                cateList: [],
                catProps: {
                    expandTrigger: 'hover',
                    label: 'cat_name',
                    value: 'cat_id',
                    children: 'children'
                },
                //
                manyTableData: [],
                //
                onlyTableData: [],
                headersObj: {
                    Authorization: window.sessionStorage.getItem('token')
                },
                previewPath: '',
                previewPathVisible: false
            }
        },
        created() {
            this.getCateList()
        }
        ,
        methods: {
            async getCateList() {
                const {data: res} = await this.$http.get('categories')
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.cateList = res.data
                console.log(this.cateList)
            },
            //
            handleChange() {
                console.log("ok")
                if (this.addFrom.goods_cat.length !== 3) {
                    this.addFrom.goods_cat = []
                }
            },
            beforeTabLeave(act, old) {
                if (this.addFrom.goods_cat.length !== 3 && old === '0') {
                    this.$message.error('')
                    return false
                }
                return true
            },
            async tabClick() {
                if (this.activeIndex === '1') {
                    const {data: res} = await this.$http.get
                    (`categories/${this.cateId}/attributes`, {params: {sel: 'many'}})

                    if (res.meta.status !== 200) {
                        return this.message.error('')
                    }
                    console.log(res.data)
                    res.data.forEach(item => {
                        item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
                    })
                    this.manyTableData = res.data
                } else if (this.activeIndex === '2') {
                    const {data: res} = await this.$http.get
                    (`categories/${this.cateId}/attributes`, {params: {sel: 'only'}})

                    if (res.meta.status !== 200) {
                        return this.message.error('')
                    }
                    console.log(res.data)
                    this.onlyTableData = res.data
                }

            },
            // 
            handlePreview(file) {
                this.previewPath = file.response.data.url
                this.previewPathVisible = true
            },
            // 
            handleRemove(file) {
                const filePath = file.response.data.tmp_path
                const i =
                    this.addFrom.pics.findIndex(x => x.pic === filePath)
                this.addFrom.pics.splice(i, 1)
            },
            //
            handSuccess(res) {
                if (res.meta.status !== 200) {
                    return this.$message.error('!')
                }
                this.addFrom.pics.push(res.data.tmp_path)
                this.$message.success('!')
                console.log(this.addFrom.pics)
                console.log(this.addFrom)
            },
             Add(){
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid){
                        return this.$message.error('!')
                    }
                    //
                  const from = _.cloneDeep(this.addFrom)
                   from.goods_cat =
                        from.goods_cat.join(',')

                    //
                    this.manyTableData.forEach(item => {
                        const newInfo =
                            {attr_id:item.attr_id,
                            attr_value:item.attr_vals.join(' ')}
                        this.addFrom.attrs.push(newInfo)
                    })
                    //
                    this.onlyTableData.forEach(item => {
                        const newInfo =
                            {attr_id:item.attr_id,
                            attr_value:item.attr_vals}
                            this.addFrom.attrs.push(newInfo)
                    })
                    from.attrs = this.addFrom.attrs
                    console.log(from)
                    //
                   const {data: res} = await this.$http.post('goods',from)
                    console.log(res)
                    if(res.meta.status !== 201){
                        return this.$message.error('!')
                    }
                    this.$message.success('!')
                    this.$router.push('/goods')
                })
            }

        },
        computed: {
            cateId() {
                if (this.addFrom.goods_cat.length === 3) {
                    return this.addFrom.goods_cat[2]
                }
                return null
            }
        }
    }
</script>

<style scoped>
  .el-steps {
    margin-top: 15px;
  }

  .el-checkbox {
    margin: 0 10px 0 0 !important;
  }

  .PImg {
    width: 100%;
  }
  .el-button{
    margin-top: 15px;
  }

</style>
